import { BasePage } from './../base/base';
import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, Platform } from 'ionic-angular';
import { ViewController } from 'ionic-angular/navigation/view-controller';
import { Geolocation } from '@ionic-native/geolocation';

/**
 * Generated class for the TaskDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
var taskMap;//地图对象
declare const AMap: any;//声明
@IonicPage()
@Component({
  selector: 'page-task-details',
  templateUrl: 'task-details.html',
})
export class TaskDetailsPage extends BasePage {
  @ViewChild('taskMap') taskMap: ElementRef;
  latitude: any;
  longitude: any;
  lnglat: any;//当前位置（转换后）
  distance: any;
  data: any;
  waming: any;
  title: any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public toastCtrl: ToastController,
    private geolocation: Geolocation,
    private platform: Platform) {
    super(navCtrl, navParams, toastCtrl);
    this.data = navParams.data.data;
    this.waming = navParams.data.wamings;
    if (this.waming == 'waitFor') {
      this.title = '待办任务查看'
    } else if (this.waming == 'history') {
      this.title = '历史任务查看'
    }
    console.log(this.data)

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad TaskDetailsPage');
    if (this.platform.is("android")) {
      this.load();
      this.gps();
    } else {
      this.load();
      this.toast("此设备不支持GPS定位");
    }


  }
  airDetails() {
    if (this.data.siteType == '2') {
      this.navCtrl.push('AirDetailsPage', { site: this.data, id: '2' });
    } else if (this.data.siteType == '1') {
      this.navCtrl.push('AutomaticDetailsPage', { site: this.data, id: '2' });
    } else if (this.data.siteType == '3') {
      this.navCtrl.push('ContaminatedDetailsPage', { site: this.data, id: '2' });
    }

  }
  gps() {
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log(resp.coords.latitude);
      console.log(resp.coords.longitude);
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
      this.transformation();
      this.addMarkers();
    }).catch((error) => {
      this.toast("此设备不支持GPS定位");

    });

  }
  load() {
    taskMap = new AMap.Map(this.taskMap.nativeElement, {
      center: [113.107681, 28.783909],
      zoom: 12, //设置地图缩放级别
      rotateEnable: true,
    });
    taskMap.setMapStyle("amap://styles/blue")


  }

  addMarkers() {
    //起
    var endIcon = new AMap.Icon({
      // 图标尺寸
      size: new AMap.Size(25, 34),
      // 图标的取图地址
      image: 'assets/imgs/dir-marker.png',
      // 图标所用图片大小
      imageSize: new AMap.Size(135, 40),
      // 图标取图偏移量
      imageOffset: new AMap.Pixel(-9, -3)
    });
    var endMarker = new AMap.Marker({
      position: new AMap.LngLat(this.lnglat[0], this.lnglat[1]),
      icon: endIcon
    });
    var startIcon = new AMap.Icon({
      size: new AMap.Size(25, 34),
      image: 'assets/imgs/dir-marker.png',
      imageSize: new AMap.Size(135, 40),
      imageOffset: new AMap.Pixel(-95, -3)
    });
    //将 icon 传入 marker
    var longitude = this.data.longitude;
    var latitude = this.data.latitude;
    var path;
    if (longitude) {
      var startMarker = new AMap.Marker({
        position: new AMap.LngLat(longitude, latitude),
        icon: startIcon,
      });
      path = [
        [this.lnglat[0], this.lnglat[1]],
        [this.data.longitude, this.data.latitude]
      ];
    }

    taskMap.clearMap();
    if (this.waming == 'waitFor') {
      if(longitude){
        taskMap.add([endMarker], [startMarker]);
        //两点直线距离
        var p1 = startMarker.getPosition();
        var p2 = endMarker.getPosition();
        this.distance = Math.round(p1.distance(p2));
  
        //两点直线样式
        var polyline = new AMap.Polyline({
          path: path,
          isOutline: true,
          borderWeight: 0,
          strokeColor: "#00FF00",
          strokeOpacity: 1,
          strokeWeight: 1,
          // 折线样式还支持 'dashed'
          strokeStyle: "dashed",
          // strokeStyle是dashed时有效
          strokeDasharray: [10, 5],
          lineJoin: 'round',
          lineCap: 'round',
          zIndex: 50,
        });
        polyline.setMap(taskMap);
      }else{
        taskMap.add([endMarker]);
      }
      
    }
    if (longitude) {
      taskMap.add([startMarker]);
    }


    //圆的绘制和编辑
    // var circle = new AMap.Circle({
    //   center: [this.longitude, this.latitude],
    //   radius: 200, //半径
    //   borderWeight: 3,
    //   strokeColor: "#FF33FF",
    //   strokeOpacity: 1,
    //   strokeWeight: 2,
    //   fillOpacity: 0.4,
    //   strokeStyle: 'dashed',
    //   strokeDasharray: [10, 10],
    //   // 线样式还支持 'dashed'
    //   fillColor: '#1791fc',
    //   zIndex: 50,
    // })
    // circle.setMap(taskMap)
    taskMap.setFitView();
  }
  transformation() {
    var GPS = {
      PI: 3.14159265358979324,
      x_pi: 3.14159265358979324 * 3000.0 / 180.0,
      delta: function (lat, lon) {

        var a = 6378245.0; //  a: 卫星椭球坐标投影到平面地图坐标系的投影因子。
        var ee = 0.00669342162296594323; //  ee: 椭球的偏心率。
        var dLat = this.transformLat(lon - 105.0, lat - 35.0);
        var dLon = this.transformLon(lon - 105.0, lat - 35.0);
        var radLat = lat / 180.0 * this.PI;
        var magic = Math.sin(radLat);
        magic = 1 - ee * magic * magic;
        var sqrtMagic = Math.sqrt(magic);
        dLat = (dLat * 180.0) / ((a * (1 - ee)) / (magic * sqrtMagic) * this.PI);
        dLon = (dLon * 180.0) / (a / sqrtMagic * Math.cos(radLat) * this.PI);
        return { 'lat': dLat, 'lon': dLon };
      },

      //GPS---高德
      gcj_encrypt: function (wgsLat, wgsLon) {
        if (this.outOfChina(wgsLat, wgsLon))
          return { 'lat': wgsLat, 'lon': wgsLon };

        var d = this.delta(wgsLat, wgsLon);
        return { 'lat': wgsLat + d.lat, 'lon': wgsLon + d.lon };
      },
      outOfChina: function (lat, lon) {
        if (lon < 72.004 || lon > 137.8347)
          return true;
        if (lat < 0.8293 || lat > 55.8271)
          return true;
        return false;
      },
      transformLat: function (x, y) {
        var ret = -100.0 + 2.0 * x + 3.0 * y + 0.2 * y * y + 0.1 * x * y + 0.2 * Math.sqrt(Math.abs(x));
        ret += (20.0 * Math.sin(6.0 * x * this.PI) + 20.0 * Math.sin(2.0 * x * this.PI)) * 2.0 / 3.0;
        ret += (20.0 * Math.sin(y * this.PI) + 40.0 * Math.sin(y / 3.0 * this.PI)) * 2.0 / 3.0;
        ret += (160.0 * Math.sin(y / 12.0 * this.PI) + 320 * Math.sin(y * this.PI / 30.0)) * 2.0 / 3.0;
        return ret;
      },
      transformLon: function (x, y) {
        var ret = 300.0 + x + 2.0 * y + 0.1 * x * x + 0.1 * x * y + 0.1 * Math.sqrt(Math.abs(x));
        ret += (20.0 * Math.sin(6.0 * x * this.PI) + 20.0 * Math.sin(2.0 * x * this.PI)) * 2.0 / 3.0;
        ret += (20.0 * Math.sin(x * this.PI) + 40.0 * Math.sin(x / 3.0 * this.PI)) * 2.0 / 3.0;
        ret += (150.0 * Math.sin(x / 12.0 * this.PI) + 300.0 * Math.sin(x / 30.0 * this.PI)) * 2.0 / 3.0;
        return ret;
      }
    };
    this.lnglat = [GPS.gcj_encrypt(this.latitude, this.longitude).lon, GPS.gcj_encrypt(this.latitude, this.longitude).lat];
  }


}



